	color: #333;
}

html.sidebar-right-opened .page-header .sidebar-right-toggle i:before {
	content: "\f054";
}

/* Page Header - Mobile */
@media only screen and (max-width: 767px) {
	.page-header {
		padding-right: 80px;
	}

	.page-header .breadcrumbs {
		display: none;
	}

	.page-header h2 {
		font-size: 16px;
		padding: 0 15px 0;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		max-width: 100%;
