	margin-bottom: 0;
	padding: 10px 0;
}

.toggle.active i.fa-minus {
	display: inline;
	color: #FFF;
}

.toggle.active i.fa-plus {
	display: none;
}

.toggle.active > label {
	background: #CCC;
	border-color: #CCC;
	color: #FFF;
}

.toggle.active > label:before {
	border: 6px solid transparent;
	border-top-color: #FFF;
	margin-top: -3px;
	right: 10px;
}

